<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="销售组织" size="mini">
          <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="clearBusiss">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="授信日期" size="mini">
          <el-date-picker v-model="timeValue" type="daterange" @change="changeDate" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="客户分类" size="mini">
          <el-select clearable v-model="params.customer_type_id" @focus="customerTypeInvok" @change="customer_type_change">
            <el-option :label="item.customer_type_name" :value="item.customer_type_id" :key="item.customer_type_id" v-for="item in customerTypeList"></el-option>
          </el-select>

        </el-form-item>
        <el-form-item label="授信类型" size="mini">
          <el-select clearable v-model="params.credit_type">
            <el-option label="固定额度" :value="1">固定额度</el-option>
            <!-- <el-option label="年度" :value="0">年度</el-option> -->
            <el-option label="临时" :value="3">临时</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户名称" size="mini">
          <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" @change="getCustomerChange" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="审核状态" size="mini">
          <el-select clearable v-model="params.auditor_status">
            <el-option label="全部" :value="0">全部</el-option>
            <el-option label="未审核" :value="1">未审核</el-option>
            <el-option label="已审核" :value="2">已审核</el-option>
            <el-option label="作废" :value="3">作废</el-option>
          </el-select>
        </el-form-item>
        <el-form-item size="mini">
          <el-checkbox style="margin-left:120px;" v-model="is_checked">已停用</el-checkbox>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addFun" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
      <el-button plain icon="el-icon-delete" @click="delFun" size="mini" v-if="authority.b_del==1">删除</el-button>
      <el-button class="iconfont icon-pass" type="primary" @click="examineFun" size="mini" v-if="authority.b_pass==1">审核</el-button>
      <el-button plain class="iconfont icon-zuofei" @click="abolishFun" size="mini" v-if="authority.b_invalid==1">作废</el-button>
      <el-button plain class="iconfont icon-guanbi1" @click="disableFun" size="mini" v-if="authority.b_disabled==1">停用</el-button>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44"></el-table-column>
        <el-table-column label="操作" fixed="right" width="100">
          <template slot-scope="scope">
            <el-button @click="examine(scope.row)" type="text" size="small" v-if="authority.b_info==1">
              查看
            </el-button>
            <el-button @click="handleEdit(scope.row)" type="text" size="small" v-if="scope.row.auditor_status == 1 && authority.b_edit == 1">
              修改
            </el-button>
            <el-button @click="handleEdit(scope, scope.row)" disabled type="text" size="small" v-else>
              修改
            </el-button>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="销售组织" width="120"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_type_name" label="客户分类" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="credit_type_name" label="授信类型"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="credit_limit" align="right" label="授信额度" width="120">
          <template slot-scope="scope">
            {{scope.row.credit_limit|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_time" label="授信时间" width="190"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" label="审核状态" width="120"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_user_name" label="审核人" width="120"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_time" label="审核时间" width="190"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="is_disabled_name" label="是否停用" width="120"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_user_name" label="创建人" width="120"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="update_user_name" label="修改人" width="120"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="update_time" label="修改时间" width="190"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注" width="120"></el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getData, opthionList } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "customeCreditmanageDefault",
  data() {
    return {
      authority: {},
      timeValue: [],
      formInlineParams: {},
      companyList: [],
      creditmanage: { options: [] }, //授信类型
      customer: { options: [] },
      TableData: {
        tableData: [],
        per_page: [30, 50, 100, 500],
        total: 10
      },
      per_page: [30, 50, 100, 500],
      pageSize: 30,
      multipleSelection: [],
      goPage: 1, //默认搜索页面
      loading: false,
      is_checked: false
    };
  },
  computed: {
    ...mapGetters(["creditmanageDefault_showGet"])
  },
  methods: {
    ...mapActions([
      "creditmanageAdd_show",
      "creditmanageSearch_show",
      "creditmanageIndexACT"
    ]),
    clearBusiss(val) {
      this.$set(this.formInlineParams, "business_id", null);
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
    },
    getCustomerChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    customer_type_change(val) {
      if (val) {
        this.params.customer_type_name = this.customerTypeList.filter(
          obj => obj.customer_type_id === val
        )[0].customer_type_name;
      }
    },
    // businessChange(val) {
    //   if (val) {
    //     this.params.employee_name = this.employeeList.options.filter(
    //       obj => obj.employee_id === val
    //     )[0].employee_name;
    //   }
    // },
    companyChange(val) {
      getDepartmentListCommon(this, this.params, 1);
    },
    onSearch() {
      if (this.is_checked == false) {
        this.params.is_disabled = "";
      } else {
        this.params.is_disabled = 1;
      }
      Object.assign(this.formInlineParams.params, this.params, {
        page: 1
      });
      this.creditmanageIndexACT({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      getData(this.formInlineParams);
    },
    onReset() {
      this.timeValue = "";
      this.$set(this, "params", {});
      this.is_checked = false;
      this.$set(this, "formInlineParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getCreditList", //接口名
        vm: this //传递的this
      });
      this.onSearch();
    },
    examineFun() {
      //审核
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].credit_id);
      }
      var creditArr = addr.toString();
      var funcan = {
        examineID: "credit_id",
        eachData: {
          credit_id: creditArr
        },
        examineApi: "customAuditorPass",
        list: {
          UrlApi: "getCreditList",
          data: this.TableData,
          esData: this.params
        }
      };
      this.$common.examineFun(funcan);
    },
    abolishFun() {
      //作废
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].credit_id);
      }
      var creditArr = addr.toString();
      var funcan = {
        examineID: "credit_id",
        eachData: {
          credit_id: creditArr
        },
        examineApi: "customAuditorInvalid",
        list: {
          UrlApi: "getCreditList",
          data: this.TableData,
          esData: this.params
        }
      };
      this.$common.examineFun(funcan);
    },
    addFun() {
      this.creditmanageAdd_show({
        // commont: "creditmanageAdd",
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/custom/creditmanage/creditmanage_add"
      });
    },
    delFun() {
      var deledata = {
        deleID: "credit_id",
        deleSelect: this.multipleSelection,
        deleApi: "delCredit",
        list: {
          UrlApi: "getCreditList",
          data: this.TableData
        }
      };
      this.$common.deletes(deledata, "删除", this);
    },
    disableFun() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要停用的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].credit_id);
      }
      var creditArr = addr.toString();
      var funcan = {
        examineID: "credit_id",
        eachData: {
          credit_id: creditArr
        },
        examineApi: "disabledCredit",
        list: {
          UrlApi: "getCreditList",
          data: this.TableData,
          esData: this.params
        }
      };
      this.$common.examineFun(funcan);
    },
    examine(row) {
      this.creditmanageSearch_show({
        // commont: "creditmanageSearch",
        credit_id: row.credit_id,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/custom/creditmanage/creditmanage_search"
      });
    },
    handleEdit(row) {
      this.creditmanageAdd_show({
        // commont: "creditmanageAdd",
        credit_id: row.credit_id,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/custom/creditmanage/creditmanage_add"
      });
    },
    changeDate(val) {
      if (val != null) {
        this.params.start_time = val[0];
        this.params.end_time = val[1];
      } else {
        this.params.start_time = "";
        this.params.end_time = "";
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      Object.assign(this.formInlineParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.formInlineParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.formInlineParams.params, {
        page: val
      });
      getData(this.formInlineParams);
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    if (
      this.creditmanageDefault_showGet &&
      this.creditmanageDefault_showGet.params
    ) {
      if (this.creditmanageDefault_showGet.params.start_time) {
        const {
          start_time,
          end_time
        } = this.creditmanageDefault_showGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      this.$set(this, "params", this.creditmanageDefault_showGet.params);
      opthionList(this, this.creditmanageDefault_showGet.params);
    }
    if (
      this.creditmanageDefault_showGet &&
      this.creditmanageDefault_showGet.goPage
    ) {
      this.params.page = this.creditmanageDefault_showGet.goPage;
      this.params.per_page = this.creditmanageDefault_showGet.pageSize;
    }
    this.$set(this, "formInlineParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getCreditList", //接口名
      vm: this //传递的this
    });
    getData(this.formInlineParams);
  }
};
</script>

